/** @format */

:root {
  /* Font */
  --primary-font: "Kumbh Sans", sans-serif;

  /*================= COLORS================= */

  /*Primary*/
  --orange-color: hsl(26, 100%, 55%);
  --pale-orange-color: hsl(25, 100%, 94%);

  /*Neutral*/
  --very-dark-blue: hsl(220, 13%, 13%);
  --dark-grayish-blue: hsl(219, 9%, 45%);
  --grayish-blue: hsl(220, 14%, 75%);
  --light-grayish-blue: hsl(223, 64%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}
.description {
  padding: 16px 24px;
  display: flex;
  flex-direction: column;
}

.description h4 {
  color: var(--orange-color);
  margin: 5px 0;
}

.description p {
  font-size: 14px;
  line-height: 1.5;
  color: var(--dark-grayish-blue);
  margin: 5px 0;
}

.description .priceContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
}

.description .priceContainer > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.description .priceContainer .price h4 {
  padding: 5px;
  border: transparent 1px solid;
  background-color: var(--pale-orange-color);
  color: orange;
  border-radius: 10%;
  margin: 0 10px;
}

.description .priceContainer h5 {
  color: var(--dark-grayish-blue);
  text-decoration: line-through;
}

.counterContainer .counter {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px 0;
  border: transparent 1px solid;
  border-radius: 10px;
  background-color: var(--light-grayish-blue);
}

.counterContainer .counter button {
  border: none;
  background-color: transparent;
  font-size: 28px;
  font-weight: 700;
  color: var(--orange-color);
}

.addToCart button {
  border: none;
  background-color: var(--orange-color);
  outline: none;
  width: 100%;
  padding: 10px 0;
  border-radius: 10px;
  color: white;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 768px) {
  .addToCart button {
    border: none;
    background-color: var(--orange-color);
    outline: none;
    width: 50%;
    padding: 10px 0;
    border-radius: 10px;
    color: white;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    
  }

  .addToCart button:hover {
    transform: translateY(-3px);
    transition: all 0.3 ease-in-out;
    cursor: pointer;
  }
  .description .addToCart {
    display: flex;
    justify-content: center;
  }
  .description .priceContainer {
    margin-bottom: 40px;
  }
}
